import { Button, MenuItem, TextField, FormControl, Divider, Box } from "@mui/material";
import { useState } from "react";
import { handleNumberStateChange } from "../common/reactUtil";
import { GameBoard, BoardSetting }  from "./GameBoard";

function SinglePlay() {
    const [numPlayer, setNumPlayer] = useState<number>(3);
    const [numCount, setNumCount] = useState<number>(3);
    const [numEnd, setNumEnd] = useState<number>(31);
    const [playerTurn, setPlayerTurn] = useState<number>(0);

    const [numSetting, setNumSetting] =  useState<BoardSetting>({numPlayer: 3, numCount: 3, numEnd:31, playerTurn: 0});
    const [started, setStarted] = useState<boolean>(false);

    const handleClick = () => {
        setNumSetting({
            numPlayer : numPlayer,
            numCount : numCount,
            numEnd : numEnd,
            playerTurn : playerTurn
        });
        setStarted(true);
    }


    return <div>
        <Box>
            <Box>
                <TextField required id="num-player" label="" type="number" value={numPlayer} onChange={(event) => handleNumberStateChange(event, setNumPlayer)}/>
                <TextField required id="num-count" label="  " type="number" value={numCount} onChange={(event) => handleNumberStateChange(event, setNumCount)}/>
                <TextField required id="num-end" label=" " type="number" value={numEnd} onChange={(event) => handleNumberStateChange(event, setNumEnd)}/>
                <FormControl sx={{width: "6em"}}>
                    <TextField required id="num-my" label=" " select value={playerTurn} onChange={(event) => handleNumberStateChange(event, setPlayerTurn)} >
                        {[...Array(numPlayer).keys()].map((_, idx) => <MenuItem key={idx} value={idx}>{idx+1}</MenuItem>)}
                    </TextField>
                </FormControl>
            </Box>
            <Button onClick={handleClick}>{started? " ": " "}</Button>
        </Box>
        <Divider variant="middle"/>
        {started? <GameBoard boardSetting={numSetting}/> : null}
    </div>
}

export default SinglePlay